import { createRouter, createWebHistory } from 'vue-router';
import Home from "@/components/Home";
import Contact from "@/components/messagerie/Contact";

const routes = [
    {
        path: '/portfolio',
        name: 'Home',
        component: Home
    },
    {
        path: '/portfolio/contact',
        name: 'Contact',
        component: Contact
    },
];

const router = createRouter({
    history: createWebHistory(), // Utilise le mode de routage "history" pour des URL propres sans #
    routes
});

export default router;
