<template>
  <div class="clickable-item" @click="handleClick">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'ItemToDiscover',
  props: {
    object: {
      type: String,
      required: true
    }
  },
  methods: {
    handleClick() {
      this.$store.dispatch('incrementScore', this.object); // Appeler l'action Vuex
    },
  },
};
</script>

<style scoped>
.clickable-item {
  cursor: pointer;
}
</style>
