<template>
  <div class="preview-container">
    <div class="header">
      <tabs :tabs="tab" @tab-selected="changeTab" :active="this.selectedTab"></tabs>
    </div>

    <div class="projects">
      <div v-if="selectedTab === 0" class="quick-view">
        <QuickView v-for="project in projects" :key="project" :project="project" @quick-view-clicked="handleQuickViewClicked">
        </QuickView>
      </div>

      <div v-else-if="selectedTab === 1" class="detailed-view" ref="detailedViewContainer">
        <DetailedView v-for="(project, index) in projects" :key="project" :project="project" :ref="setDetailedViewRef(index, project.name)"></DetailedView>
      </div>
    </div>
  </div>
</template>

<script>
import Tabs from "@/components/Tabs";
import DetailedView from "@/components/DetailedView";
import QuickView from "@/components/QuickView";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Preview",
  components: {QuickView, DetailedView, Tabs },
  data() {
    return {
      projects: [
        {name: 'Portfolio',image: require('@/assets/Portfolio.png'), description: 'Application Web pr√©sentant mes diff√©rents projets personnels', tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'Calorie',image: require('@/assets/calorie.png'), description: 'Application Web de suivit de d√©penses caloriques', tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'Timer',image: require('@/assets/timer.png'), description: 'Application mobile d\'entrainement de musculation avanc√©', tags: {front: ['React Native',], back: []}},
        {name: 'E-commerce',image: require('@/assets/e-commerce.png'), description: 'Application Web d\'un site de type e-commerce (front-end)', tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'It\'s A Watch !',image: require('@/assets/Watch.png'), description: 'Application mobile pour choisir des films ou s√©rie √† regarder en groupe', tags: {front: ['Flutter',], back: []}},
        {name: 'Chifoumi',image: require('@/assets/chifoumi.png'), description: 'Jeux mobile utilisant la fonctionnalit√© de Wifi Direct', tags: {front: ['XML',], back: ['Java']}},
        {name: 'GoToTown',image: require('@/assets/GoToTown.png'), description: 'Application mobile pour afficher et sauvergarder ses centres d\'int√©r√™t', tags: {front: ['XML',], back: ['Java']}},
        {name: 'Lykhubs',image: require('@/assets/Lykhubs.png'), description: 'Cr√©ation d\'un lecteur vid√©o pour un r√©seau social', tags: {front: ['HTML', 'CSS', 'Javascript',], back: []}},
        {name: 'Lumberjack',image: require('@/assets/Haptique.png'), description: 'Jeux interactif jouable avec une souris haptique', tags: {front: ['Qt'], back: ['C++']}},
        {name: 'Twitch Extension',image: require('@/assets/Extension.png'), description: 'Application web permettant la cr√©ation et gestion d\'extension pour Twitch', tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
      ],
      tab: ['fa-th', 'fa-square'],
      selectedTab: 0,

      detailedViews: [],
      detailedViewsPosition: [],

      scrollPositions: {
        quickView: 0,
        detailedView: 0,
      },

      scrollEnabled: true,
    }
  },
  watch: {
    selectedTab(value) {
      if(value===1)
      {
        new Promise((resolve) => {
          const checkDetailedView = () => {
            if (this.$refs.detailedViewContainer) {
              resolve();
            } else {
              setTimeout(checkDetailedView, 1); // Check again after a short delay
            }
          };
          checkDetailedView();
        }).then(() => {
          this.$refs.detailedViewContainer.addEventListener('scroll', () => {
            this.handleScroll()
          })
        });
      }
    }
  },
  methods: {
    changeTab(tab) {
      this.selectedTab = tab;
    },

    setDetailedViewRef(index, name) {
      return (el) => {
        this.detailedViews[index] = {el: el, name: name}; // Assign the element directly
      };
    },

    handleQuickViewClicked(project) {
      // Trouvez l'index du projet cliqu√©
      const index = this.projects.findIndex((p) => p === project);

      // Utilisez une promesse pour attendre que l'√©l√©ment DetailedView soit rendu
      new Promise((resolve) => {
        // Changez de tab (tab=1)
        this.changeTab(1);

        const checkDetailedView = () => {
          if (this.detailedViews[index]) {
            resolve();
          } else {
            setTimeout(checkDetailedView, 1); // Check again after a short delay
          }
        };
        checkDetailedView();
      }).then(() => {
        const detailedView = this.detailedViews[index];

        if (detailedView) {
          this.scrollEnabled = false;
          setTimeout(() => {
            this.scrollEnabled = true;
          }, 1000);

          detailedView.el.$el.scrollIntoView({ behavior: "smooth", block: "start" });
        }
      });
    },

    handleScroll() {
      if(this.scrollEnabled)
      {
        this.detailedViews.forEach((detailedView) => {
          const componentRect = detailedView.el.$el.getBoundingClientRect();
          const containerRect = this.$refs.detailedViewContainer.getBoundingClientRect();

          if (
              componentRect.top >= containerRect.top && // D√©but de l'√©l√©ment visible
              componentRect.bottom <= containerRect.bottom // Fin de l'√©l√©ment visible
          ) {
            this.$store.dispatch('incrementScore', detailedView.name)
          }
        });
      }
    },
  }
}
</script>

<style scoped>
.preview-container {
  display: flex;
  flex-direction: column;

  flex: 1;

  margin: 20px 20px 0 20px;

  overflow-y: auto;
}

.header {
  height: 35px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  background-color: var(--medium-grey);
}

.projects {
  display: flex;

  flex: 1;

  overflow-y: auto;
  overflow-x: hidden;
}

.detailed-view {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 20px;

  flex: 1;

  padding: 10px 25px; /* Le 10px doit √™tre report√© dans DetailedView pour que le scroll se r√©alise correctement */

  overflow-y: auto;

  background-color: var(--medium-grey);
}

.detailed-view > *:not(:last-child){
  padding-bottom: 20px;
  border-bottom: 2px solid var(--dark-grey);
}

.quick-view{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1px;

  flex: 1;

  overflow-y: auto;
}

/* CUSTOM SCROLLBAR */
/* Custom scrollbar track */
.detailed-view::-webkit-scrollbar-track, .quick-view::-webkit-scrollbar-track {
  background-color: var(--light-grey);
}

/* Custom scrollbar thumb */
.detailed-view::-webkit-scrollbar-thumb, .quick-view::-webkit-scrollbar-thumb {
  background-color: var(--dark-grey);
}

/* Hide the default scrollbar */
.detailed-view::-webkit-scrollbar, .quick-view::-webkit-scrollbar {
  width: 6px;
}
/* CUSTOM SCROLLBAR */

</style>
