<template>
  <div class="tabs-container">
    <button
        v-for="(tab, index) in tabs"
        :key="index"
        @click="selectTab(index)"
        :class="{ active: activeTab === index }"
    >
      <i :class="['fas', tab]"></i>
    </button>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Tabs',
  props: {
    tabs: {
      type: Array,
      required: true
    },
    active: {
      type: Number,
      required: true
    },
  },
  data() {
    return {
      activeTab: this.active
    };
  },
  watch: {
    active(newActiveTab) {
      this.activeTab = newActiveTab;
    }
  },
  methods: {
    selectTab(index) {
      this.activeTab = index;
      this.$emit('tab-selected', index); // √âmet l'√©v√©nement tab-selected avec l'onglet s√©lectionn√©
    }
  }
};
</script>

<style scoped>
.tabs-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  flex: 1;
}

.tabs-container button {
  flex: 1;

  padding: 5px 10px;

  cursor: pointer;
  outline: none;

  border-bottom: solid 1px var(--grey);

  background-color: transparent;
}

.tabs-container button i{
  font-size: 24px;
}

.tabs-container button.active {
  border-bottom: 2px solid var(--primary-color);
}

</style>
