<template>
  <div class="header-container">
    <div class="left">
      <p>9:41 AM</p>
    </div>

    <div class="middle">
      <h3 v-if="showMessage" :class="{'fade-in-out': showMessage}" ref="message">{{ currentMessage }}</h3>
    </div>

    <div class="right">
      <i v-if="score === 0" class="fas fa-battery-empty not-found"></i>
      <i v-if="score > 0 && score < 5" class="fas fa-battery-quarter found-one"></i>
      <i v-if="score > 4 && score < 14" class="fas fa-battery-three-quarters exploring"></i>
      <i v-if="score > 13" class="fas fa-battery-full all-founded"></i>
    </div>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Header",
  mounted() {
    setTimeout(() => {
      this.displayRandomMessage();
    }, (Math.random() * 2000) + 500 );
  },
  computed: {
    score() {
      return this.$store.getters.getScore;
    },
  },
  data() {
    return {
      messages: [
        'Explorer pour trouver l\'ü•ö',
        'Une id√©e ? => ‚úâÔ∏è'
      ],
      currentMessage: "",
      showMessage: false,
      displayedMessages: [],
    };
  },
  methods: {
    getRandomMessage() {
      const availableMessages = this.messages.filter(
          message => !this.displayedMessages.includes(message)
      );
      if (availableMessages.length === 0) {
        // Tous les messages ont √©t√© affich√©s, r√©initialisez la liste
        this.displayedMessages = [];
      }
      const randomIndex = Math.floor(Math.random() * availableMessages.length);
      return availableMessages[randomIndex];
    },
    displayRandomMessage() {
      this.showMessage = false; // Cache le message pr√©c√©dent
      setTimeout(() => {
        this.currentMessage = this.getRandomMessage();
        this.displayedMessages.push(this.currentMessage); // Ajoutez le message actuel √† la liste des messages affich√©s
        this.showMessage = true; // Affiche le nouveau message

        this.$nextTick(() => {
          // D√©clenche le fade-in en for√ßant un recalcul de style
          window.getComputedStyle(this.$refs.message).opacity;
          this.$refs.message.style.opacity = 1;
          this.$refs.message.style.top = '0px';

          // G√©n√©rez un d√©lai al√©atoire entre 0 et 5 secondes
          const randomDelay = Math.random() * 5000;

          setTimeout(() => {
            // D√©clenche le fade-out
            this.$refs.message.style.opacity = 0;
            this.$refs.message.style.top = '20px';

            setTimeout(() => {
              if (this.displayedMessages.length === this.messages.length) {
                // Tous les messages ont √©t√© affich√©s, r√©initialisez la liste
                this.displayedMessages = [];
              }
              this.displayRandomMessage(); // Affiche le prochain message
            }, randomDelay);
          }, 3000);
        });
      }, 1000); // Attendez une seconde avant de passer au prochain message
    },
  },
};
</script>

<style scoped>
.header-container {
  height: 30px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin: 0 20px;
}

.left {
  height: 100%;
  flex: 1;

  display: flex;
  align-items: center;
  justify-content: flex-start;

}

.left p{
  text-transform: uppercase;
  font-weight: bold;

  margin-top: 4px;
}

.middle {
  height: 100%;
  width: 210px;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 0 0 15px 15px;

  margin: 0 20px;

  overflow: hidden;

  background-color: var(--primary-color);
}

.middle h3{
  color: var(--white);
}

.right {
  height: 100%;
  flex: 1;

  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.right i{
  font-size: 22px;

  margin-right: 10px;
}

.not-found{
  color: var(--not-found);
}

.found-one{
  color: var(--found-one);
}

.exploring{
  color: var(--exploring);
}

.all-founded{
  color: var(--all-founded);
}

.middle h3.fade-in-out {
  position: relative;
  opacity: 0;
  top: -20px;
  transition: opacity 0.5s ease, top 0.5s ease;
}
</style>
