<template>
  <ItemToDiscover :object="project.name">
    <div class="quick-view-container" :style="backgroundImageStyle" @click="handleClick">
      <div class="content">
        <h2>{{ project.name }}</h2>

        <p>{{ project.description }}</p>
      </div>
    </div>
  </ItemToDiscover>
</template>

<script>
import ItemToDiscover from "@/components/ItemToDiscover";

export default {
  name: "QuickView",
  components: {ItemToDiscover},
  props: {
    project: {
      required: true
    }
  },
  data() {
    return {
      backgroundSize: 'contain',
    };
  },
  computed: {
    backgroundImageStyle() {
      // V√©rifiez si project.image est d√©fini
      if (this.project.image) {
        // Utilisez l'URL de l'image comme fond
        return {
          backgroundImage: `url('${this.project.image}')`,
        };
      } else {
        // Aucune image n'est d√©finie, utilisez un fond par d√©faut ou laissez-le vide
        return {};
      }
    },
  },
  methods: {
    handleClick() {
      this.$emit("quick-view-clicked", this.project); // √âmet l'√©v√©nement avec l'√©l√©ment DOM
    },
  }
}
</script>

<style scoped>
.quick-view-container {
  aspect-ratio: 1 / 1;

  display: flex;
  flex-direction: row;

  cursor: pointer;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--light-grey);
}

.content{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;

  gap: 8px;
  padding: 20px 0;

  overflow: hidden;

  color: var(--white);
}

.content:hover{
  background-color: var(--dark-shadow);
}

.content h2{
  text-align: center;
}

.content p{
  font-size: 12px;

  text-align: center;
}

.quick-view-container .content{
  opacity: 0;
}

.quick-view-container:hover .content{
  opacity: 1;
}

</style>
