<template>
  <div class="detailed-view-container">
    <h2>{{project.name}}</h2>

    <p>{{project.description}}</p>

    <div class="view" :style="backgroundImageStyle">

    </div>

    <div class="tags">
      <div v-for="tag in project.tags.front" :key="tag" class="tag front">
        <p>{{tag}}</p>
      </div>

      <div v-for="tag in project.tags.back" :key="tag" class="tag back">
        <p>{{tag}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailedView",
  props: {
    project: {
      required: true
    }
  },
  data() {
    return {
      imageHeight: 280 // Hauteur initiale
    }
  },
  computed: {
    backgroundImageStyle() {
      // V√©rifiez si project.image est d√©fini
      if (this.project.image) {
        // Utilisez l'URL de l'image comme fond
        return {
          backgroundImage: `url('${this.project.image}')`,
          height: `${this.imageHeight}px`,
        };
      } else {
        // Aucune image n'est d√©finie, utilisez un fond par d√©faut ou laissez-le vide
        return {};
      }
    },
  },
  mounted() {
    this.calculateBackgroundImageStyle();
  },
  methods: {
    calculateBackgroundImageStyle() {
      if (this.project.image) {
        const img = new Image();
        img.src = this.project.image;

        img.onload = () => {
          const imgWidth = img.width;
          const imgHeight = img.height;
          const imgRatio = imgWidth / imgHeight;

          // Met la variable √† jour
          this.imageHeight = 280 - (Math.abs(1 - imgRatio) * 85);
        };
      }
    },
  },
}
</script>

<style scoped>
.detailed-view-container {
  display: flex;
  flex-direction: column;

  flex: 1;

  gap: 8px;

  scroll-margin-top: 10px; /* Pour mettre le scroll au d√©but du composant lors du changement de tabulation */
}

.detailed-view-container p{
  font-size: 16px;
}

.view {
  height: 280px;

  display: flex;
  flex-direction: column;

  border-radius: 5px;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.tags{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  gap: 6px;
}

.tag{
  display: flex;
  flex-direction: column;

  padding: 4px 8px;
  border-radius: 5px;
}

.tag p{
  color: var(--white);

  font-weight: bold;
}

.front{
  background-color: var(--front-color);
}

.back{
  background-color: var(--back-color);
}

</style>
