// src/store.js
import { createStore } from 'vuex';

export default createStore({
    state: {
        score: 0,
        clickedItems: [], // Ajoutez cette variable pour suivre les √©l√©ments cliqu√©s
    },
    mutations: {
        incrementScore(state) {
            state.score++;
        },
        addToClickedItems(state, itemId) {
            state.clickedItems.push(itemId);
        },
    },
    actions: {
        incrementScore({ commit, state }, itemId) {
            if (!state.clickedItems.includes(itemId)) {
                commit('incrementScore');
                commit('addToClickedItems', itemId);
            }
        },
    },
    getters: {
        getScore(state) {
            return state.score;
        },
    },
});
