<template>
  <div class="contact-container">
    <div class="date">
      <p>Aujourd'hui 08:01</p>
    </div>

    <div class="conversation">

      <div class="presentation">
        <div class="photo">

        </div>

        <div class="message">
          <p>N'h√©sitez pas √† me contacter pour discuter de vos projets et collaborer ensemble !</p>
        </div>
      </div>

      <div v-if="!this.messageSended" ref="inputsContainer" class="inputs fade-in">
        <Input label="Nom" class="input" @input-change="updateName" required></Input>

        <Input label="Email" type="email" class="input" @input-change="updateEmail" required></Input>
      </div>

      <div v-if="this.messageSended" class="recapitulatif">
        <div class="input">
          <h3>Nom:</h3>
          <p>{{this.name}}</p>
        </div>

        <div class="input">
          <h3>Email:</h3>
          <p>{{this.email}}</p>
        </div>

        <div class="input">
          <h3>Message:</h3>
          <pre>{{this.message}}</pre>
        </div>

      </div>

      <div v-if="this.messageSended"  ref="finalMessage" class="presentation fade-in">
        <div class="photo">

        </div>

        <div class="message">
          <p>Merci pour votre message, je reviendrais vers vous tr√®s rapidement ;)</p>
        </div>
      </div>

    </div>

    <div v-if="!this.messageSended" class="message-input" ref="messageInput" :class="{'disable' : name === '' || email === '' }">
      <textarea @input="adjustHeight" v-model="message" :disabled="name === '' || email === ''"></textarea>

      <ItemToDiscover :object="message">
        <button @click="sendMessage" :disabled="name === '' || email === ''">Envoyer</button>
      </ItemToDiscover>
    </div>
  </div>
</template>

<script>
import Input from "@/components/Input";
import ItemToDiscover from "@/components/ItemToDiscover";
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Contact",
  components: {ItemToDiscover, Input},
  data() {
    return {
      name: "",
      email: "",
      message: "",
      messageSended: false,
    }
  },
  mounted() {
    setTimeout(() =>{
      this.addShowClass(this.$refs.inputsContainer)
    }, 500)
  },
  methods: {
    addShowClass(container) {
      console.log(container);

      if (container) {
        container.classList.add("show");
      }
    },

    // Mettez √† jour la valeur de 'name' √† partir de l'√©v√©nement Input
    updateName({ name, value }) {
      if (name === "Nom") {
        this.name = value;
      }
    },

    // Mettez √† jour la valeur de 'email' √† partir de l'√©v√©nement Input
    updateEmail({ name, value }) {
      if (name === "Email") {
        this.email = value;
      }
    },

    sendMessage()
    {
      this.messageSended = true;

      setTimeout(() =>{
        this.addShowClass(this.$refs.finalMessage)
      }, 900)
    },

    adjustHeight() {
      const textarea = this.$refs.messageInput.querySelector("textarea");
      textarea.style.height = "auto"; // R√©initialisez d'abord la hauteur

      // Cr√©ez un √©l√©ment temporaire pour calculer la hauteur de ligne
      const tempElement = document.createElement("div");
      tempElement.style.fontSize = getComputedStyle(textarea).fontSize;
      tempElement.style.lineHeight = getComputedStyle(textarea).lineHeight;
      tempElement.style.visibility = "hidden";
      tempElement.style.position = "absolute";
      tempElement.innerHTML = "text"; // Contenu de test pour calculer la hauteur de ligne
      document.body.appendChild(tempElement);
      const lineHeight = tempElement.clientHeight;

      const maxLines = 10;

      // Calculez la hauteur maximale
      const maxHeight = lineHeight * maxLines;

      // Calculez la nombre de lignes dans la textarea
      let lines = textarea.scrollHeight / lineHeight

      lines = Math.floor(lines);

      // Ajustez la hauteur de la textarea
      textarea.style.height = lines <= maxLines ? lines <=2 ? `auto` : `${(lines * lineHeight)}px` : `${maxHeight}px`;

      // Supprimez l'√©l√©ment temporaire
      document.body.removeChild(tempElement);
    },
  },
}
</script>

<style scoped>
.contact-container{
  display: flex;
  flex-direction: column;

  gap: 15px;
  padding: 20px;

  overflow-y: auto;

  flex: 1;
}

.date{
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.date p{
  font-size: 20px;
  font-weight: bold;
}

.conversation {
  display: flex;
  flex-direction: column;

  flex: 1;

  gap: 25px;

}

.presentation {
  display: flex;
  flex-direction: row;
  align-items: center;

  gap: 10px;
}

.photo{
  min-width: 40px;
  min-height: 40px;

  border-radius: 360px;

  background-color: var(--grey);
}

.message{
  border: 1px solid var(--black);
  border-radius: 20px;

  padding: 6px 12px;
}

.message p{
  font-size: 16px;
}

.inputs, .recapitulatif{
  display: flex;
  flex-direction: column;
  align-items: end; /* Si on met end, doit mettre une width a .input*/

  gap: 10px;

}

.recapitulatif p, pre{
  font-size: 14px;

  white-space: pre-wrap;
}

.input {
  width: 80%; /* Si on met end, doit mettre une width a .input*/

  padding: 14px 20px;

  border-radius: 20px;

  background-color: var(--light-grey);
}

.message-input{

  display: flex;
  flex-direction: row;

  border: 1px solid var(--black);
  border-radius: 20px;

  overflow: auto;

  background-color: var(--white);
}

.message-input textarea{
  display: flex;
  flex: 1;

  margin-left: 12px;
  padding: 12px 12px 12px 0;

  border: none;
  outline: none;
  resize: none;
}

.message-input button{
  height: 100%;

  color: var(--white);

  font-weight: bold;

  padding: 12px 6px;

  background-color: var(--black);
}

.disable{
  border-color: var(--grey);
}

/* SCROLLBAR */

/*          */

/* Animation */
/* Ajoutez une transition pour l'effet de fondu */
.fade-in.show {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

/* R√©glez l'opacit√© initiale √† 0 pour l'effet de fondu */
.fade-in {
  opacity: 0;
}
/*           */

</style>
